<script>
import data from "@/assets/numbers.json"
import retrieveValue from "../src/mixins/retrieve.js"
import calculateCostAction from "../src/mixins/calculate.js"
import populate from "../src/mixins/populateTable.js"

//contagem de itens dentro do json
const ITENS = 13;


export default{
  mixins:[retrieveValue, calculateCostAction, populate],
  data(){
    return{
      costs:["processamentoPF", "processamentoPJ"],
      values:[],
      dados:[
        {id:0,key:"validacaoEmail",  value:null, title:"Validação de Email", label:"Quantidade de validações"},
        {id:1,key:"acionamentoEmail",  value:null, title:"Acionamento por Email", label:"Quantidade de acionamentos"},
        {id:2,key:"acionamentoSms",  value:null, title:"Acionamento por SMS",label:"Quantidade de acionamentos"},
        {id:3,key:"acionamentoWhatsapp",  value:null, title:"Acionamento por WhatsApp",label:"Quantidade de acionamentos"},
        {id:4,key:"carta",  value:null, title:"Acionamento por Carta",label:"Quantidade de acionamentos"},
        {id:5,key:"negativacao",  value:null, title:"Negativação",label:"Quantidade de negativações"},
        {id:6,key:"indicadoresPJ",  value:null, title:"Indicadores PJ",label:"Quantidade de empresas que terão indicadores"},
        {id:7,key:"indicadoresPF", value:null, title:"Indicadores PF",label:"Quantidade de pessoas que terão indicadores"},
        {id:8,key:"enriquecimentoPJ",  value:null, title:"Enriquecimento PJ",label:"Quantidade de empresas para enriquecer"},
        {id:9,key:"enriquecimentoPF" , value:null, title:"Enriquecimento PF",label:"Quantidade de pessoas para enriquecer"},
        {id:10,key:"enriquecimentoEmail", value:null, title:"Enriquecimento de Email",label:"Quantidade de emails para enriquecer"},
      ],
      costRecommended:[],
      choice:true,
      email: null,
      companies:null,
      persons:null,
      recommended: "true",
      relative: "true"
    }
  },
  
  methods: {
    /**
     * Retorna o valor em int para quantidade de PFs e PJs;
     * @param {*} index Referente ao tipo de valor, 0 = custo, 1 = minimo e 2 = referencia;
     */
    tired(index){
      let a = parseInt(this.companies);
      let b = parseInt(this.persons);
      if(a > 0 || b > 0)
        return (this.calculateCostAction(index, 'f')*b)+(this.calculateCostAction(index, 'j')*a)
      else{
        return 0;
      }
    },

    /**
     * 
     * @param {*} as 
     * @param {*} bs 
     */
    sum(as,bs){
      //as -
      var a = parseInt(as)
      var b = parseInt(bs)
      if(a>0 && b>0) return parseInt(a)+parseInt(b); 
      if(a === 0 && b > 0){
        return b
      }
      if(a > 0 && b === null){
        return a
      }
      return 1;
    },
    /**
     * 
     * @param {*} cases Especificação de ação por tipo de pessoa
     * @param {*} tipo Referente ao tipo de valor, 0 = custo, 1 = minimo e 2 = referencia;
     */
    cost (cases, tipo) {
      let custo = 0;
      for(let i = 0; i < this.costs.length; i++){
        for(let j = 0; j < ITENS; j++){
          if(this.costs[i]===data[tipo][j].item && (data[tipo][j].key===cases||data[tipo][j].key==='fj')){
            if(data[tipo][j].item === 'acionamentoEmail'){
              custo += data[tipo][j].value*this.email;
            }
            // passar os valores direto, sem conversao e ja voltar o resultado
            if(data[tipo][j].item==='negativacao' && tipo > 0){
              custo += data[tipo][j].value * this.negativacao(this.costs)
            }
            else{
              custo += data[tipo][j].value
            }
          }
        }
      }
      return custo;
    },

    costImproved(cases,tipo){
      let custo = 0;
      for(let i = 0; i < this.costs.length; i++){
        for(let j = 0; j < ITENS; j++){
          if(this.costs[i]===data[tipo][j].item && (data[tipo][j].key===cases||data[tipo][j].key==='fj')){
            let teste = this.dados.findIndex(x => x.key === this.costs[i])
            if(teste>=0){
              if(data[tipo][j].item==='negativacao' && tipo > 0){
                custo += data[tipo][j].value * this.dados[teste].value * this.negativacao(this.costs)
              }
              else custo += data[tipo][j].value*this.dados[teste].value;
            }
          }
        }
      }
      if(custo >= 0)return custo;
      else return 0;
    },

    /**
     * 
     * @param {*} costs 
     */
    negativacao(costs){
      if(costs.length === 3) return 1
      else return 0.4
    },
    /**
     * Converte o numero para uma notação com espaçamento de 3. Para frações fica com duas casas decimais;
     * @param {*} n Numero a ser convertido;
     */
    numeral(n){
      return n.toLocaleString('pt-BR', {minimumFractionDigits: 2,maximumFractionDigits: 3})
    },
    findInArray(key){
      var item = this.costs.find(item => item === key)
      if(item === undefined) return false
      else return true;
    }
  }
}
</script>
<template>
<body>
    <div>
      <h1>Calculadora Mais</h1>
      <h2>Quantidade de pessoas</h2>
      <a>Pessoas Fisicas</a>
      <b-form-input  v-model="persons" placeholder="Quantidade de Pessoas Fisicas" type="number"></b-form-input>
      <a>Pessoas Juridicas</a>
      <b-form-input  v-model="companies" placeholder="Quantidade de Pessoas Juridicas" type="number"></b-form-input>
      <b-form-group  label="Ações Recomendadas" v-slot="{ ariaDescribedby }">
        <b-form-radio-group buttons>
          <b-form-radio v-model="recommended" :aria-describedby="ariaDescribedby" name="some-radios" value="true">Sim</b-form-radio>
          <b-form-radio v-model="recommended" :aria-describedby="ariaDescribedby" name="some-radios" value="false">Não</b-form-radio>
        </b-form-radio-group>
      </b-form-group>
      <b-form-group label="Custos">
        <b-form-radio-group buttons>
          <b-form-radio v-model="relative" name="those-radios" value="true">Sim</b-form-radio>
          <b-form-radio v-model="relative" name="those-radios" value="false">Não</b-form-radio>
        </b-form-radio-group>
      </b-form-group>
    </div>
    <hr>
    <div v-if="recommended=='false'">
      <h2>Itens</h2>
      <b-form-group v-slot="{ ariaDescribedby }">
        <b-form-checkbox-group
          id="checkbox-group-1"
          v-model="costs"
          :aria-describedby="ariaDescribedby"
          name="flavour-1">
          <div v-for="dado in dados" :key="dado.id">
            <b-form-checkbox v-model="costs" :value="dado.key">{{dado.title}}</b-form-checkbox>
            <b-form-input v-if="findInArray(dado.key)" v-model="dado.value" type="number" :placeholder="dado.label"></b-form-input>
          </div>
        </b-form-checkbox-group>
      </b-form-group>
    </div>
    <div v-else>
        <h1>Ações Recomendadas</h1>
        <h2>Acionamento por Email</h2>
        <a> R${{ 4 * retrieveValue('acionamentoEmail',0) }}/pessoa</a><br>
        <a>4 emails por pessoa.</a>
        <h2>Acionamento por SMS</h2>
        <a> R${{ 2 * retrieveValue('acionamentoSms',0) }}/pessoa</a><br>
        <a>2 SMS por pessoa.</a>
        <h2>Acionamento por Wpp</h2>
        <a> R${{ 2 * retrieveValue('acionamentoWhatsapp',0) }}/pessoa</a><br>
        <a>2 acionamentos por pessoa.</a>
        <h2>Acionamento por Carta</h2>
        <a> R${{ (retrieveValue('carta',0)).toFixed(2) }}/pessoa</a><br>
        <a>1 carta por pessoa.</a>
        <hr>
    </div>
     <div v-if="recommended === 'true'">
      <h2>Preços</h2>
      <b-table-simple hovers small responsive>
        <b-thead>
          <b-tr>
            <b-th>Tipo</b-th>
            <b-th >CPF</b-th>
            <b-th >CNPJ</b-th>
            <b-th >Total</b-th>
          </b-tr>
        </b-thead>
        <b-tbody>
          <b-tr v-if="relative == 'true'">
            <b-th>Custo</b-th>
            <b-td >R${{ calculateCostAction(0,'f').toFixed(2) }}</b-td>
            <b-td >R${{ calculateCostAction(0,'j').toFixed(2) }}</b-td>
            <b-td colspan="4">R${{ numeral(tired(0)) }}</b-td>
          </b-tr>
          <b-tr v-if="relative == 'true'">
            <b-th >Valor Minimo</b-th>
            <b-td >R${{ calculateCostAction(1,'f').toFixed(2) }}</b-td>
            <b-td >R${{ calculateCostAction(1,'j').toFixed(2) }}</b-td>
            <b-td colspan="4">R${{ numeral(tired(1)) }}</b-td>
          </b-tr>
          <b-tr>
            <b-th>Valor de Referencia</b-th>
            <b-td >R${{ calculateCostAction(2,'f').toFixed(2) }}</b-td>
            <b-td >R${{ calculateCostAction(2,'j').toFixed(2) }}</b-td>
            <b-td colspan="4">R${{ numeral(tired(2)) }}</b-td>
          </b-tr>
        </b-tbody>
      </b-table-simple>
    </div>

    <div v-if="recommended == 'false'">
      <h2>Preços</h2>
      <b-table-simple hover small caption-top responsive>
        <b-thead head-variant="dark">
          <b-tr>
            <b-th colspan="2">Tipo</b-th>
            <b-th colspan="2">CPF</b-th>
            <b-th colspan="3">CNPJ</b-th>
            <b-th colspan="3">Total</b-th>
          </b-tr>
        </b-thead>
        <b-tbody>
          <b-tr v-if="relative == 'true'">
            <b-th>Custo</b-th>
            <b-td colspan="2">R${{ numeral(costImproved('f',0)) }}</b-td>
            <b-td colspan="3">R${{ numeral(costImproved('j',0)) }}</b-td>
            <b-td colspan="4">R${{ numeral((costImproved('f',0)*persons)+(costImproved('j',0)*companies)) }}</b-td>
          </b-tr>
          <b-tr v-if="relative == 'true'">
            <b-th >Valor Minimo</b-th>
            <b-td colspan="2">R${{ numeral(costImproved('f',1)) }}</b-td>
            <b-td colspan="3">R${{ numeral(costImproved('j',1)) }}</b-td>
            <b-td colspan="4">R${{ numeral((costImproved('f',1)*persons)+(costImproved('j',0)*companies)) }}</b-td>
          </b-tr>
          <b-tr>
            <b-th>Valor de Referencia</b-th>
            <b-td colspan="2">R${{ numeral(costImproved('f',2)) }}</b-td>
            <b-td colspan="3">R${{ numeral(costImproved('j',2)) }}</b-td>
            <b-td colspan="4">R${{ numeral((costImproved('f',2)*persons)+(costImproved('j',0)*companies)) }}</b-td>
          </b-tr>
          
        </b-tbody>
      </b-table-simple>
    </div>
</body>
</template>

<style>
@import url('https://fonts.googleapis.com/css2?family=Kodchasan');
