export default{
    methods:{
        /**
         * Popula uma tabela com formato do BootStrap com os valores passados;
         * @param {*} cost Valor de custo dentro do JSON;
         * @param {*} min Valor minimo dentro do JSON;
         * @param {*} ref Valor de referencia detro do JSON;
         * @returns Retorna a tabela populada com os valores especificados;
         */
        populate(cost,min,ref){
            let processed = [];
            return processed[
                {
                    Custo: cost[0],
                    Minimo: min[0],
                    Referencia: ref[0]
                },
                {
                    Custo: cost[1],
                    Minimo: min[1],
                    Referencia: ref[1]
                },
                {
                    Custo: cost[2],
                    Minimo: min[2],
                    Referencia: ref[2]
                }
            ]
        }
    }
}