import data from "@/assets/numbers.json"

export default{
    methods:{
        calculateCostAction:function( index, nature ){
            var cost = 0;
            for(let i = 0; i < 13; i++){
                if(data[index][i].item === 'acionamentoEmail' || data[index][i].item === 'enriquecimentoEmail' || data[index][i].item === 'validacaoEmail' ){
                    cost += data[index][i].value*4;
                }
                else if(data[index][i].item === 'acionamentoSms'){
                    cost += data[index][i].value*2;
                }
                else if(data[index][i].item === 'acionamentoWhatsapp'){
                    cost += data[index][i].value*2;
                }
                else if(nature === data[index][i].key || data[index][i].key === 'fj'){
                    cost += data[index][i].value;
                }
            }
            return cost;
        }
    }
}